@import 'variables';

@font-face {font-family: 'fa-fontello'; font-weight: normal; font-style: normal; font-display: auto;
	src: url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.woff2') format('woff2'),
	url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.woff') format('woff'),
	url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.ttf') format('truetype'), 
	url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.svg#fa-fontello') format('svg'),
	url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.eot'),
	url('https://rxpharmacyassurance.com/wp-content/plugins/enfold-fast/assets/fonts/fa-fontello.eot?#iefix') format('embedded-opentype');
	} #top .avia-font-fa-fontello, body .avia-font-fa-fontello, html body [data-av_iconfont='fa-fontello']:before{ font-family: 'fa-fontello'; }

*{
	box-sizing: border-box;
	font-family: $body-font-family;
}
#container, html.embed{
	background: transparent !important;
}
/* Logo */
#logo a {
	background-image: inherit;
	min-height: inherit;
	height: inherit;
}

body{
	font-family: $body-font-family;
}
/* Backgrounds */
html, #logo,
#container,
.wufoo input.text, .wufoo textarea.textarea,
.wufoo .instruct,
form li.focused {
	background: inherit;
}

/* Borders */
#container,
.wufoo .info, 
.wufoo .instruct {
	border: inherit;
}

/* Typography */
body,
.wufoo,
.wufoo .info h2,
.wufoo .info div,
.wufoo .section h3,
.wufoo .section div,
.wufoo li div, .wufoo li div label, .wufoo li span label,
.wufoo input.text, .wufoo textarea.textarea, .wufoo select.select, .wufoo label.choice, 
.wufoo label.choice,
.wufoo .instruct {
	background: inherit;
	color: inherit;
	font-size: inherit;
	font-family: $body-font-family;
}

/* Drop Shadows */
#container {
	-webkit-box-shadow: inherit;
	-moz-box-shadow: inherit;
	-o-box-shadow: inherit;
	box-shadow: inherit;
}


#header{
	display: none;
}


.wufoo label.desc, .wufoo legend.desc, .wufoo li span label, form li div span{
	font-weight: $bold!important;
	color: $darkblue2!important;
	font-family: $body-font-family;
	font-size: 18px!important;
}

.wufoo li span label{
	color: #acacac;
}
.wufoo .field{
	border: 0;
	border-bottom: 1px solid #acacac;
	background-color: white;
	padding: 12px 0;
}


.wufoo label.desc > div{
	font-weight: 400;
}

.wufoo li.focused{
	background: transparent!important;
}

.wufoo li.focused .field{
	outline: none;
}

.wufoo li .field{
	width: 100%;
	margin: 0;
}
.wufoo .buttons{
	text-align: right;
}
.wufoo input.btTxt{
	background: #9a9c84;
	border: 0 !important;
	font-size: 14px;
	padding: 20px 40px 17px;
	color: white;
	border-radius: 0px;
	cursor: pointer;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-weight: bold;
	margin-right: 0 !important;
	transition: opacity ease 0.25s!important;
}


.wufoo input.btTxt{
	transition: all linear 0.1s;
}
.wufoo input.btTxt:hover{
	background: #00a7e4;
}

.confirm h2 {
	color: #00a7e4;
	border: 2px solid;
	text-transform: uppercase;
	padding: 100px;
	font-weight: bold;
	letter-spacing: 2.5px;
	font-family: $body-font-family;
	line-height: 1.4;
}

.wufoo li{
	width: 100%;
	padding-left: 0 !important;
	padding-right: 0 !important;
	margin-bottom: 15px;
}
form li.leftHalf, form li.rightHalf {
	width: 49% !important;
}

form .instruct {
	width: 100%;
	position: relative;
	visibility: visible;
	opacity: 1;
	left: auto !important;
	padding: 0;
	margin: 30px 0;
}

form .instruct em{
	color: #a5a9ac !important;
	font-size: 16px;
	line-height: 24px;
}

@media only screen and (max-width: 480px) {
	
	form li.leftHalf, form li.rightHalf{
		width: 100% !important;
	}
	
}

label{
	display: none!important;
}

.wufoo li .text,
.wufoo li .textarea{
	border: 2px solid;
	border-color: #CBDBE9;
	border-radius: 25px;
	font-size: 16px;
	padding: 13.5px 25px;
	color: $darkblue2!important;
	background-color: rgba($color: $white, $alpha: 0.44);
	transition: background-color ease 0.25s, border-color ease 0.25s;
	font-weight: 900!important;
}

.wufoo li .text::-webkit-input-placeholder,
.wufoo li textarea::-webkit-input-placeholder{
	color: $darkblue2!important;
	font-weight: $regular!important;
}

.wufoo li{
	&:not(:last-child){
		margin-bottom: 10px!important;
	}
}
.wufoo .buttons div{
	text-align: left;
}

.wufoo .focused .text,
.wufoo .focused .textarea{
	background-color: $white;
	border-color: $pink4;
}

.wufoo .buttons div .btTxt{
	display: inline-block;
    min-width: 213px;
    padding: 17.5px 30.5px;
    border-radius: 30px;
    background: linear-gradient(90deg, #c84bf4 0%, #6a88df 100%);
    text-align: center;
    color: $white;
    text-transform: uppercase;
    font-weight: $bold;
    font-family: $heading-font-family;
    font-size: 14px;
    line-height: 1.1;
    letter-spacing: calc(( 14px / 1000 ) * 100 );
    &:hover{
        text-decoration: none;
        opacity: 0.8;
    }
}

.wufoo li fieldset .desc{
	text-transform: none!important;
	letter-spacing: 0!important;
	font-weight: $bold!important;
	color: $darkblue2!important;
	font-size: 18px!important;
}

.wufoo li fieldset .radio,
.wufoo li fieldset .checkbox{
	width: auto!important;
	margin-top: 15px;
}

.wufoo li fieldset .checkbox{
position: relative;
}

.wufoo li fieldset label{
	display: block!important;
	text-transform: none!important;
	letter-spacing: 0!important;
	font-size: 16px!important;
}

.wufoo li fieldset label span{
	text-transform: none!important;
	letter-spacing: 0!important;
	font-size: 16px!important;
	font-weight: $regular!important;
}

.wufoo li fieldset .radio{
	position: relative;
}

.wufoo li fieldset .radio:before,
.wufoo li fieldset .checkbox:before{
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 15px;
	height: 15px;
	border: 1px solid $lightblue5;
	background-color: $white;
	border-radius: 100%;
	top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.wufoo li fieldset .radio:before{
	border-radius: 100%;
}

.wufoo li fieldset .checkbox:before{
	border-radius: 2px;
}

.wufoo li fieldset .radio:checked:after{
    content: '';
    opacity: 1;
    position: absolute;
	width: 8px;
	height: 8px;
	background-color: $blue2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
	z-index: 1;
	border-radius: 100%;
}

.wufoo li fieldset .checkbox:checked:before{
    content: '\e804';
    font-family: 'fa-fontello';
    display: flex;
    justify-content: center;
    font-size: 15px;
    color: $blue2;
}

.wufoo li .instruct{
	padding: 15px 16px;
	border: 1px solid $lightblue5!important;
	border-radius: 4px;
	margin-top: 17px;
	font-size: 12px!important;
} 
.wufoo li .instruct small{
	font-size: 100%!important;
	color: $darkblue2!important;
}

.wufoo li fieldset div span{
    display: flex;
    align-items: center;
	margin-left: 2px;
}

.wufoo li fieldset div span:not(:last-child){
    margin: 10px 0 10px 2px;
}

.wufoo li fieldset div span .radio,
.wufoo li fieldset div span .checkbox{
    margin-top: 0; 
}

.wufoo li fieldset label{
    margin-top: 0!important;
    padding: 0;
}